* {
  font-family: 'Lato', sans-serif;
}

body {
  margin: auto auto;
  padding: 60px;
  max-width: 1475px;
}

input{
  width: 50%;
  font-weight:400;
}

.card{
  border-radius: 10px;
  border: 1px solid lightgray;
  display:block;
  box-sizing: border-box;
  margin-bottom: 10px;
  margin-right: 10px;
  max-width: 248px;
  height: auto;
  overflow: hidden;

}

.card p{
  padding: 5px;
  text-align: center;
  word-wrap: break-word;
  white-space:pre-wrap;
}
.card img{
  aspect-ratio: auto 248 / 140;
  height: auto;
  width: auto;
  object-fit: contain;
  width: 248px;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}